.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  /* align-items: center; */
  /* align-content: space-around; */
  /* align-content: stretch; */
  /* align-content: flex-end; */
  height: 100vh;
}

.topHint {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  padding: 12px;
  margin: 16px;
  line-height: 1.5;
  border-radius: 4px;
  background-color: #accaee;
  cursor: pointer;
}

.topHint > div {
  display: inline;
}

.topHintClose {
  float: right;
  color: #285473;
}

.chiliz {
  display: -ms-flexbox;
  display: flex;
  height: 60vh;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}

.hint {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  margin: 12px;
  color: #d4d4d4;
  font-weight: bold;
  text-align: center;
}

.title {
  text-transform: capitalize;
  font-size: 48px;
}

.subtitle {
  font-size: 18px;
}

